import React from "react";
import ReactDOM from "react-dom";
import { createStore, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import axios from "axios";
import axiosMiddleware from "redux-axios-middleware";
import rootReducer from "./reducers";
import "bulma/css/bulma.css";
import "./core.css";
import App from "./App";
import {ITEMS_PER_PAGE, SB_FOLDER_ID} from './config';


// ScienceBase does not have CORS set up properly to request from non-local domains
// So we make a JSONP request, strip off the function call, and pass it along as JSON
const client = axios.create({
  baseURL: "https://www.sciencebase.gov/catalog/items",
  responseType: "text",
  transformResponse: [(rawdata) => {
    return JSON.parse(rawdata.replace('getSBJSON(', '').replace(');', ''));
  }],
  params: {
    fields: 'title,summary,previewImage,browseCategories,hasChildren',
    format: 'jsonp',
    max: ITEMS_PER_PAGE,
    folderId: SB_FOLDER_ID
  }
});

let store = createStore(rootReducer, applyMiddleware(axiosMiddleware(client)));
ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById("Root")
);



